import React from 'react'
import '../styles/Footer.css'
import logoWhite from '../assets/images/logo-white.png'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebookF, faTwitter, faInstagram, faWhatsapp} from '@fortawesome/free-brands-svg-icons'

function Footer() {
    return (
        <footer>
            <div className="max-width">
                <div className="container">
                    <div className="logo">
                        <img src={logoWhite} alt="" />
                    </div>
                    <div className="text">
                        Lorem ipsum dolor sit amet consectetur adipisicing elit. Dicta temporibus, sit alias quibusdam magnam neque vitae!
                    </div>
                    <div className="devider"></div>
                    <div className="bottom-footer">
                        <div className="copy">
                            Copyright 2021. All rights reserved
                        </div>
                        <div className="social-icons">
                            <FontAwesomeIcon icon={faFacebookF} />
                            <FontAwesomeIcon icon={faInstagram} />
                            <FontAwesomeIcon icon={faTwitter} />
                            <FontAwesomeIcon icon={faWhatsapp} />
                        </div>
                    </div>
                </div>
            </div>
        </footer>
    )
}

export default Footer
