import React from 'react'
import '../styles/About2.css'
import about2Img1 from '../assets/images/about2.jpg'
import aboutIcon1 from '../assets/icons/database.png'

function About2() {
    return (
        <section className="about2">
            <div className="max-width">
                <div className="container">
                    <div className="left-side" data-aos="fade-right"
                        data-aos-duration="2000">
                        <div className="title">
                            Nurture Your Customers Using Advanced CRM
                        </div>
                        <div className="text">
                            Smratseo is a brand of digital agency. Competen novate synergstic vortas through forward strategic theme areas Compelling extend super was that Proactive myocardinate vertical strategic.
                        </div>
                        <div className="cards">
                            <div className="card">
                                <div className="icon-container">
                                    <img src={aboutIcon1} alt="" />
                                </div>
                                <div className="text-card">
                                    Build Relationships with Companies
                                </div>
                            </div>
                            <div className="card">
                                <div className="icon-container">
                                    <img src={aboutIcon1} alt="" />
                                </div>
                                <div className="text-card">
                                    Complete Customer Management
                                </div>
                            </div>
                            <div className="card">
                                <div className="icon-container">
                                    <img src={aboutIcon1} alt="" />
                                </div>
                                <div className="text-card">
                                    Prepare for Follow Ups
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="right-side" data-aos="fade-left"
                        data-aos-duration="2000">
                        <img src={about2Img1} alt="" />
                    </div>
                </div>
            </div>
        </section>
    )
}

export default About2
