import React from 'react'
import '../styles/About1.css'
import aboutImg1 from '../assets/images/about1.jpg'
import aboutIcon1 from '../assets/icons/database.png'

function About1() {
    return (
        <section className="about1" id="about">
            <div className="max-width">
                <div className="container">
                    <div className="left-side" data-aos="fade-right"
                        data-aos-duration="2000">
                        <img src={aboutImg1} alt="" />
                    </div>
                    <div className="right-side" data-aos="fade-left"
                        data-aos-duration="2000">
                        <div className="title">
                            Empower Your Workforce With HR Manager
                        </div>
                        <div className="text">
                            Smratseo is a brand of digital agency. Competen novate synergstic vortas through forward strategic theme areas Compelling extend super was that Proactive myocardinate vertical strategic.
                        </div>
                        <div className="cards">
                            <div className="card">
                                <div className="icon-container">
                                    <img src={aboutIcon1} alt="" />
                                </div>
                                <div className="text-card">
                                    Everybody Gets a Dashboard
                                </div>
                            </div>
                            <div className="card">
                                <div className="icon-container">
                                    <img src={aboutIcon1} alt="" />
                                </div>
                                <div className="text-card">
                                    Complete Leave Management
                                </div>
                            </div>
                            <div className="card">
                                <div className="icon-container">
                                    <img src={aboutIcon1} alt="" />
                                </div>
                                <div className="text-card">
                                    Human Resource Reports
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default About1
