import React, { useState } from 'react'
import '../styles/Navbar.css'
import logoWhite from '../assets/images/logo-white.png'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars, faTimes } from '@fortawesome/free-solid-svg-icons'

function Navbar() {

    const [navbar, setNavbar] = useState(false);

    const changeBaackgroundNavbar = () => {
        if (window.scrollY >= 50) {
            setNavbar(true)
        } else {
            setNavbar(false)
        }
    }

    window.addEventListener('scroll', changeBaackgroundNavbar);

    function ScrollToHome() {
        var element = document.querySelector("#home");
        element.scrollIntoView({ behavior: 'smooth', block: 'end' });
    }

    function ScrollToContact() {
        var element = document.querySelector("#contact");
        element.scrollIntoView({ behavior: 'smooth', block: 'end' });
    }

    function ScrollToServices() {
        var element = document.querySelector("#services");
        element.scrollIntoView({ behavior: 'smooth', block: 'end' });
    }

    function ScrollToTestimonial() {
        var element = document.querySelector("#testimonial");
        element.scrollIntoView({ behavior: 'smooth', block: 'end' });
    }

    function ScrollToAbout() {
        var element = document.querySelector("#about");
        element.scrollIntoView({ behavior: 'smooth', block: 'end' });
    }

    function MenuClick(){
        var element = document.getElementById("menu");
        element.classList.toggle("active");
        var element2 = document.getElementById("menu-icon");
        element2.classList.toggle("active");
    }

    return (

        <nav className={navbar ? 'fixed' : ''}>
            <div className="max-width">
                <div className="container">
                    <div className="logo" onClick={ScrollToHome}>
                        <img src={logoWhite} alt="" />
                    </div>
                    <ul className="menu" id="menu">
                        <li onClick={ScrollToHome}>Home</li>
                        <li onClick={ScrollToServices}>Services</li>
                        <li onClick={ScrollToAbout}>About</li>
                        <li onClick={ScrollToTestimonial}>Testimonial</li>
                        <li onClick={ScrollToContact}>Contact Us</li>
                    </ul>
                    <div className="menu-icon" id="menu-icon" onClick={MenuClick}>
                        <FontAwesomeIcon icon={faBars} />
                        <FontAwesomeIcon icon={faTimes} />
                    </div>
                </div>
            </div>
        </nav>
    )
}

export default Navbar
