import React from 'react'
import '../styles/ContactUs.css'
import imgContact from '../assets/images/desktop-contact.png'

function ContactUs() {
    return (
        <section className="contactUs" id="contact">
            <div className="max-width">
                <div className="container">
                    <div className="right-side" data-aos="fade-right"
                        data-aos-duration="2000">
                        <img src={imgContact} alt="" />
                    </div>
                    <div className="left-side" data-aos="fade-left"
                        data-aos-duration="2000">
                        <div className="title">
                            Contact Us
                        </div>
                        <div className="sub-title">
                            Lorem Ipsum is simply dummy text of the printing and typesetting industry!
                        </div>
                        <div className="inputs">
                            <div className="row1">
                                <input type="text" placeholder="Name" />
                                <input type="text" placeholder="Phone" />
                            </div>
                            <input type="email" placeholder="Email" />
                            <textarea name="" id="" cols="30" rows="6" placeholder="Message"></textarea>
                        </div>
                        <div className="btns">
                            <button className="primary-btn">
                                Send Message
                            </button>
                        </div>
                    </div>
                </div>

            </div>
        </section>
    )
}

export default ContactUs
