import React, { useState } from 'react'
import './styles/App.css';
import AOS from 'aos';
import 'aos/dist/aos.css';
import Home from './components/Home'
import Navbar from './components/Navbar';
import Services from './components/Services';
import About1 from './components/About1';
import About2 from './components/About2';
import Testimonial from './components/Testimonial';
import ContactUs from './components/ContactUs';
import Footer from './components/Footer';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCaretUp } from '@fortawesome/free-solid-svg-icons'

AOS.init({
  once:true
});

function App() {

  const [scrollUp, setScrollUpBtn] = useState(false);

  const changeBaackgroundNavbar = () => {
      if (window.scrollY >= 50) {
        setScrollUpBtn(true)
      } else {
        setScrollUpBtn(false)
      }
  }

  window.addEventListener('scroll', changeBaackgroundNavbar);

  function ScrollToHome() {
    var element = document.querySelector("#home");
    element.scrollIntoView({ behavior: 'smooth', block: 'end'});
  }


  return (
      <div className="App">
        <div class= {scrollUp ? 'scroll-up-btn fixed' : 'scroll-up-btn'} onClick={ScrollToHome}>
            <div class="btn-container">
              <FontAwesomeIcon icon={faCaretUp} />
            </div>
        </div>
        <Navbar/>
        <Home/>
        <Services/>
        <About1/>
        <About2/>
        <Testimonial/>
        <ContactUs/>
        <Footer/>
    </div>
  );
}

export default App;
