import React from 'react'
import '../styles/Home.css'
import home_img1 from '../assets/images/01.png'
import shapes1 from '../assets/images/01.jpg'
import shapes2 from '../assets/images/02.jpg'
import shapes3 from '../assets/images/03.jpg'
import shapes4 from '../assets/images/04.jpg'
import shapes5 from '../assets/images/05.jpg'

function home() {
    return (
        <section className="home__banner" id="home">
            <div className="max-width">
                <div className="container">
                    <div className="left-side" id="homeLeft">
                        <div className="title1" data-aos="fade-left"
                            data-aos-duration="2000">
                            Best Application for <br />
                            Your School.
                        </div>
                        <div className="text" data-aos="fade-left"
                            data-aos-duration="2000" data-aos-delay="500">
                            As companies move their business operations to the Internet, new data trails are created.
                        </div>
                        <div className="btns">
                            <button className="primary-btn" data-aos="fade-up"
                                data-aos-duration="2000" data-aos-delay="1000">
                                About Us
                            </button>
                            <button className="second-btn" data-aos="fade-up"
                                data-aos-duration="2000" data-aos-delay="1200">
                                Contact Us
                            </button>
                        </div>
                    </div>
                    <div className="right-side" data-aos="zoom-in"
                        data-aos-duration="2000" data-aos-delay="200" data-aos-anchor="#homeLeft">
                        <img src={home_img1} alt="" />
                        <div className="rounded-shape">
                            <div className="round6"></div>
                            <div className="round5"></div>
                            <div className="round4"></div>
                            <div className="round3"></div>
                            <div className="round2"></div>
                            <div className="round1"></div>
                        </div>
                        <div className="shapes">
                            <img className="shape1" src={shapes1} alt="" data-aos="zoom-in"
                                data-aos-duration="2000" data-aos-delay="1500" data-aos-anchor="#homeLeft" />
                            <img className="shape2" src={shapes2} alt="" data-aos="zoom-in"
                                data-aos-duration="2000" data-aos-delay="1800" data-aos-anchor="#homeLeft" />
                            <img className="shape3" src={shapes3} alt="" data-aos="zoom-in"
                                data-aos-duration="2000" data-aos-delay="2000" data-aos-anchor="#homeLeft"/>
                            <img className="shape4" src={shapes4} alt="" data-aos="zoom-in"
                                data-aos-duration="2000" data-aos-delay="2200" data-aos-anchor="#homeLeft"/>
                            <img className="shape5" src={shapes5} alt="" data-aos="zoom-in"
                                data-aos-duration="2000" data-aos-delay="2400" data-aos-anchor="#homeLeft"/>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default home
